import { ActionSheet, Cell } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicActionSheet() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell clickable title="" rightIcon={<ArrowRight />} onClick={() => setOpen(true)} />
      <ActionSheet open={open} onSelect={() => setOpen(false)} onClose={setOpen}>
        <ActionSheet.Action value="1" name="" />
        <ActionSheet.Action value="2" name="" />
        <ActionSheet.Action value="3" name="" />
      </ActionSheet>
    </>
  )
}

function ActionSheetWithCancel() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ActionSheet
        open={open}
        onSelect={() => setOpen(false)}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      >
        <ActionSheet.Action value="1" name="" />
        <ActionSheet.Action value="2" name="" />
        <ActionSheet.Action value="3" name="" />
        <ActionSheet.Button type="cancel"></ActionSheet.Button>
      </ActionSheet>
    </>
  )
}

function ActionSheetWithDescription() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ActionSheet
        open={open}
        onSelect={() => setOpen(false)}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      >
        <ActionSheet.Header></ActionSheet.Header>
        <ActionSheet.Action value="1" name="" />
        <ActionSheet.Action value="2" name="" />
        <ActionSheet.Action value="3" name="" />
        <ActionSheet.Button type="cancel"></ActionSheet.Button>
      </ActionSheet>
    </>
  )
}

function ActionSheetWithStatuses() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell clickable title="" rightIcon={<ArrowRight />} onClick={() => setOpen(true)} />
      <ActionSheet
        open={open}
        onSelect={() => setOpen(false)}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      >
        <ActionSheet.Action value="1" style={{ color: "#ee0a24" }} name="" />
        <ActionSheet.Action value="2" disabled name="" />
        <ActionSheet.Action value="3" loading name="" />
        <ActionSheet.Button type="cancel"></ActionSheet.Button>
      </ActionSheet>
    </>
  )
}

export default function ActionSheetDemo() {
  return (
    <Page title="ActionSheet " className="action-sheet-demo">
      <Block variant="card" title="">
        <BasicActionSheet />
        <ActionSheetWithCancel />
        <ActionSheetWithDescription />
      </Block>
      <Block variant="card" title="">
        <ActionSheetWithStatuses />
      </Block>
    </Page>
  )
}
